import { Component, OnInit } from '@angular/core';
import { PurchaseOrderService } from 'src/app/api/controllers/PurchaseOrder';
import { CookieService } from 'ngx-cookie-service';
import { InventoryService } from 'src/app/api/controllers/Inventory';
import { SharedService } from 'src/app/shared/services/shared.service';
import { StaffService } from 'src/app/api/controllers/Staff';

@Component({
	selector: 'app-inventory-stock-adjustments',
	templateUrl: './inventory-stock-adjustments.component.html',
	styleUrls: ['./inventory-stock-adjustments.component.scss']
})
export class InventoryStockAdjustmentsComponent implements OnInit {

	public settings: any = {};
	public warehouseData: any = [];
	public materialItemData: any = [];
	public stock: any = {};
	isAssetLoaded: boolean;
	public staffList:any =[];
	ItemStartIndex: any = 0;
	ItemEndIndex: any;
	totalItems: any;
	itemLimit: any = 5;
	inventoryList: any = [];
	selectedItem:any=[];
	issuedStaffId:any =[];
	constructor(
		private purchaseOrderService: PurchaseOrderService,
		private cookieService: CookieService,
		private inventoryService: InventoryService,
		private sharedService: SharedService,
		private staffService:StaffService
	) { }

	ngOnInit(): void {
		this.getAllStaff();
		this.stock.warehouseId = "";
		this.stock.outTowhomissuedStaffId="";
		this.getMaterials();
		this.getAllGetAllWarehouse();
		this.getAllItem();
		this.settings = {
			singleSelection: true,
			text: "Search",
			selectAllText: 'Select All',
			unSelectAllText: 'UnSelect All',
			enableSearchFilter: true,
			badgeShowLimit: 3,
			// lazyLoading: true
		};
		
	}

	onItemSelect(item: any) {
		console.log(item);
		this.stock.materialId = item.id;
		this.getAvailableStock();
		this.getAllItem();
	}
	OnItemDeSelect(item: any) {
		console.log(item);
		console.log(this.selectedItems);
	}
	selectedItems(selectedItems: any) {
		console.log(selectedItems);
	}
	onSelectAll(items: any) {
		console.log(items);
	}
	onDeSelectAll(items: any) {
		console.log(items);
	}
	

	onItemSelectByStaff(item: any) {
		console.log(item);
		this.stock.outTowhomissuedStaffId = item.id;
	}
	OnItemDeSelectByStaff(item: any) {
		console.log(item);
		console.log(this.selectedItems);
	}
	selectedItemsByStaff(selectedItems: any) {
		console.log(selectedItems);
	}
	onSelectAllByStaff(items: any) {
		console.log(items);
	}
	onDeSelectAllByStaff(items: any) {
		console.log(items);
	}
	getAllGetAllWarehouse() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId')
		};
		this.purchaseOrderService.getAllWarehouseByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				// this.warehouseData = [];
				this.warehouseData = res && res.length > 0 ? res : [];
			}
		});
	}

	getMaterials() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
		};
		this.purchaseOrderService.getMaterialsByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				if (res && res.length > 0) {
					res.filter(val => {
						this.materialItemData.push({
							'id': val.materialId,
							'itemName': val.material1
						});
					})
				}
			}
		});
	}
	addStock() {
		let stockReqobj: any = {};
		stockReqobj = {
			"apartmentId": this.cookieService.get('apartmentId'),
			"inventoryTransactionTypeId": 13,
			"date": new Date(),
			"warehouseId": Number(this.stock.warehouseId),
			"materialId": Number(this.stock.materialId),
			// "sku": "string",
			"availableStockQty": this.stock.availableStockQty,
			// "unitofMeasureId": 9,
			"transactionTypeId": 13,
			"outTotalQtyConsumedBy": Number(this.stock.outTotalQtyConsumedBy),
			"outReasonForConsuming": this.stock.outReasonForConsuming,
			"outTowhomissuedStaffId": Number(this.stock.outTowhomissuedStaffId),
			"outDateofconsumption": this.stock.outDateofconsumption,
			"outOutgoingReason": this.stock.outOutgoingReason, // missing
			"outTotalcost": this.stock.outTotalcost, //missing..
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date(),
			"updatedBy": null,
			"updatedOn": new Date(),
		}
		let params: any = {};
		params.inventoryTransaction = stockReqobj;
		this.inventoryService.addInventoryTransaction(params).subscribe((res: any) => {
			 if (res) {
				this.sharedService.setAlertMessage("Stock Item added successfully");
				this.stock = {};
				this.isAssetLoaded = false;
				// this.getAllItem();	
				this.selectedItem =[];
				this.issuedStaffId= []
				this.inventoryList =[];
				this.stock.inventoryList ='';

			}
		});
	}
	getAvailableStock() {
		if(!this.stock.materialId || !this.stock.warehouseId){
         return;
		}
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
			warehouseId :this.stock.warehouseId,
            materialId:this.stock.materialId
		};
		this.inventoryService.getStockCountbyMaterialId(queryParamBase).subscribe((res: any) => {
		
			 if( res && res.length > 0 ){
			 this.stock.availableStockQty = res[0].totalStockQty;
			}
			
		});
	}

	getAllStaff() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId')
		};
		this.isAssetLoaded = true;
		this.staffService.getAllStaffsByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				this.isAssetLoaded = false;
				// this.staffList = res ? res : [];
				if(res && res.length >0){
				res.filter(val => {
					this.staffList.push({
						'id': val.staffId,
						'itemName': val.firstName
					});
				})
			  }
			}
		});
	}
	getAllItem() {
		if(!this.stock.warehouseId || !this.stock.materialId ){
			return;
		}
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
			warehouseId:this.stock.warehouseId,
			materialId:this.stock.materialId
		};
		// this.isAssetLoaded = true;
		this.inventoryService.getAllInventoryTransactionByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				// this.warehouseData = [];
				this.inventoryList = res && res.length > 0 ? res : [];
				console.log(this.inventoryList);
				this.totalItems = this.inventoryList.length;			
				if (this.totalItems > this.itemLimit) {
					this.ItemEndIndex = this.itemLimit;
				}
				else {
					this.ItemEndIndex = this.totalItems;
				}

			}
		});
	}
	getIndexParams(event) {
		this.ItemStartIndex = event.ItemStartIndex;
		this.ItemEndIndex = event.ItemEndIndex;
		this.itemLimit = event.itemLimit;
	}
}

