  getMaterials() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
		};
		this.purchaseOrderService.getMaterialsByApartmentId(queryParamBase).subscribe((res: any) => {
		
				if (res && res.length > 0) {
				  this.materialList = res;
        }
        if(this.materialList && this.materialList.length >0){
          this.materialList.filter(val =>{
            this.filterMaterial.push({
              'id': val.materialId, 
             'itemName': val.material1 });
           
          })
        }
			
		});
	}


