  
  import { Component, OnInit, ViewChild } from '@angular/core';
  import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { InventoryService } from 'src/app/api/controllers/Inventory';
import { CookieService } from 'ngx-cookie-service';
import { PurchaseOrderService } from 'src/app/api/controllers/PurchaseOrder';



@Component({
  selector: 'app-inventory-current-details',
  templateUrl: './inventory-current-details.component.html',
  styleUrls: ['./inventory-current-details.component.scss']
})
export class InventoryCurrentDetailsComponent implements OnInit {
  
 
    inventoryHeader:any
    inventoryHistoryData:any;
    gridSourceData:any;
    warehouseData:any = [];
    searchWareHouseId:any='';
    isActiveColor:any;
    currIndex:any;
    @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  historyStatus: string;
  
    constructor(
      private inventoryService :InventoryService,
      private purchaseOrderService:PurchaseOrderService,
      private cookieService:CookieService
    ) { }
  
    ngOnInit() {

      var cellsrenderer = (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      }
  
      var columnrenderer = (value) => {
        return '<div style="padding: 14px">' + value + '</div>';
      }


      this.inventoryHeader = [{
            text: 'Item No',
           datafield: 'materialId',
           width: 100,
           cellsrenderer: cellsrenderer,
           renderer: columnrenderer
       }, {
        text: 'Item Description',
           datafield: 'materialName',
           minwidth: 100,
           cellsrenderer:cellsrenderer,
           renderer: columnrenderer
       }, {
        text: 'Warehouse Name',
           datafield: 'warehouseName',
           cellsrenderer:cellsrenderer,
           minwidth: 100,
           renderer: columnrenderer
       }, {
        text: 'Qty on Hand',
           datafield: 'stockQty',
           cellsrenderer: cellsrenderer,
           minwidth: 100,
           renderer: columnrenderer
       }, 
       {
        text: 'Reorder Level',
           datafield: 'reorderTriggerlevel',
           cellsrenderer: cellsrenderer,
           minwidth: 100,
           renderer: columnrenderer
       }, {
        text: 'Status',
           datafield: 'stockStatus',
          //  cellsrenderer:cellsrenderer,
           minwidth: 100,
           cellsrenderer: (row, column, value) => {
            return '<div class="jqx-custom-inner-cell"><span class="dots rt-1 '+ getClassName(value) +'"></span>' + value + '</div>'
           },
           renderer: columnrenderer
       }];
      this.getCurrentInventory();
      this.getAllGetAllWarehouse(     );
    }


    getPrintParams(event){
      this.datagrid.exportdata(event, 'InventoryCurrentDetails');
    }
    getCurrentInventory(){
      let params:any = {};
      params.ApartmentId = parseInt(this.cookieService.get('apartmentId'));
       this.purchaseOrderService.getAllInventoryCurrentCountByApartmentId(params).subscribe((res:any) => {
        if(res) {
        this.gridSourceData = {
          localdata:  res.length > 0 ? res:[],
          datatype: "array"
        }  
        this.inventoryHistoryData = new jqx.dataAdapter(this.gridSourceData);
      }
       });
      
    }

    getAllGetAllWarehouse() {
      let queryParamBase: any = {};
      queryParamBase = {
        apartmentId: this.cookieService.get('apartmentId'),
        
      };
      this.purchaseOrderService.getAllWarehouseByApartmentId(queryParamBase).subscribe((res: any) => {
        if (res) {
          // this.warehouseData = [];
          this.warehouseData = res && res.length > 0 ? res : [];
          
        }
      });
    }
  
    onStatusFilter(){
      if(this.historyStatus != ""){
        let filterGroup = new jqx.filter();
        let filterOperator = 1;
        let filterValue = this.historyStatus;
        let filterCondition = 'contains';
        let filterData = filterGroup.createfilter('stringfilter', filterValue, filterCondition);
        filterGroup.operator = 'or';
        filterGroup.addfilter(filterOperator, filterData);
        this.datagrid.showfiltercolumnbackground(false);
        this.inventoryHeader.forEach(item => {
          if(item.datafield == 'stockStatus') {
            
            this.datagrid.addfilter(item.datafield, filterGroup, true);
          }
        })
        this.datagrid.applyfilters();
      }
      else {
        this.datagrid.clearfilters();
      }
    }
  
    onSelectFilter(){
      if(this.searchWareHouseId != ""){
        let filterGroup = new jqx.filter();
        let filterOperator = 1;
        let filterValue = this.searchWareHouseId;
        let filterCondition = 'contains';
        let filterData = filterGroup.createfilter('stringfilter', filterValue, filterCondition);
        filterGroup.operator = 'or';
        filterGroup.addfilter(filterOperator, filterData);
        this.datagrid.showfiltercolumnbackground(false);
        this.inventoryHeader.forEach(item => {
          if(item.datafield == 'warehouseName') {
            this.datagrid.addfilter(item.datafield, filterGroup, true);
          }
        })
        this.datagrid.applyfilters();
      }
      else {
        this.datagrid.clearfilters();
      }
    }
  }
  function getClassName(value){
    // var name = value.split('-');
    // return name[0].toLowerCase();    
    return value=="Low" ? 'normal-status' : 'low';
  }
  (window as any).getClassName = getClassName;
  