  getUomList() {
		let queryParamBase: any = {};
		queryParamBase = {
			ApartmentId: this.cookieService.get('apartmentId'),
			LookupTypeId: 173,
		};		
		this.lookupService.getLookupValueByLookupTypeId(queryParamBase).subscribe((res: any) => {
			if (res) {
      this.uomData = res?res:[];
			}
		});
  }
  
  getVendorList() {
	let queryParamBase: any = {};
	queryParamBase = {
		apartmentId: this.cookieService.get('apartmentId'),
	};
	this.vendorService.getVendorByApartmentId(queryParamBase).subscribe((res: any) => {
		if (res) {
       this.vendorData = res?res:[];
		}
	});
}




  getMaterials() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
		};		
		this.purchaseOrderService.getMaterialsByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
			this.itemData = res?res:[];
			this.tempItemData = this.itemData;
			setTimeout(() => {
				if(this.inventoryCategoryList && this.inventoryCategoryList.length > 0){
					this.itemData.filter (inventoryItem => {
						this.inventoryCategoryList.filter( val =>{
							if(inventoryItem.inventoryCategoryId == val.id ){
							 console.log(val);
							 inventoryItem.subCategories = val.subCategory;
							}
							// inventoryItem.subCategory =val.subCategory?val.subCategory:[];				  
				  })
				})
				}	
				console.log(this.itemData,'item');
		}, 500)
			 
			
			this.totalItems = this.itemData.length;

			if (this.totalItems > this.itemLimit) {
				this.ItemEndIndex = this.itemLimit;
			}
			else {
				this.ItemEndIndex = this.totalItems;
			}


			}
		});
	}
	

	getAllGetAllWarehouse() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
			
		};
		this.isAssetLoaded = true;
		this.purchaseOrderService.getAllWarehouseByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				// this.warehouseData = [];
				this.warehouseData = res && res.length > 0 ? res : [];
				
			}
		});
	}
