	getAllInventory() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
			lookupTypeId: 174,
			subCategoryLookupTypeId: 175,
		};
		this.isAssetLoaded = true;
		this.lookupService.getSubcategory(queryParamBase).subscribe((res: any) => {
			if (res) {
				this.isAssetLoaded = false;
				this.inventoryCategoryList = res ? res : [];
			}
		});
	}

	addItem( data ){
	  let reqObj ={} ;
	  reqObj =  {
		"materialId": 0,
		"apartmentId": parseInt(this.cookieService.get('apartmentId')),
		"material1": data.material1,
		"materialNo": Number(data.materialNo),
		"unitOfMeasureId": Number(data.unitOfMeasureId),
		// "comments": null,
		"isActive": true,
		"insertedBy": parseInt(this.cookieService.get('userId')),
		"insertedOn": new Date().toISOString,
		"updatedBy": 0,
		"updatedOn":  new Date().toISOString,
		"isInventoryManaged": true,
		"sku": data.sku,
		"preferredVendorId": Number(data.preferredVendorId),
		"inventoryCategoryId":  Number(data.inventoryCategoryId),
		"inventorySubCategoryId": Number(data.inventorySubCategoryId),
		"materialType": Number(data.materialType),
		"reorderTriggerlevel": Number(data.reorderTriggerlevel),
		"reorderQuantity": Number(data.reorderQuantity),
		"serialNo": 0,
		"notes": data.notes,
		// "customfields": null,
		"warehouseId": Number(data.warehouseId)
	  }
		let params :any ={};
		 params.material = reqObj;
		this.purchaseOrderService.addMaterial(params).subscribe((res: any) => {
		console.log(res, res);
		if (res) {
		  this.sharedService.setAlertMessage("Inventory Item added successfully");
		  this.isAssetLoaded = false;
		   this.row={};
		this.getMaterials();
		}	
	  });
	}

	updateItem(data){
		let reqObj ={} ;
		reqObj =  {
		  "materialId": data.materialId,
		  "apartmentId": parseInt(this.cookieService.get('apartmentId')),
		  "material1": data.material1,
		  "materialNo": Number(data.materialNo),
		  "unitOfMeasureId": Number(data.unitOfMeasureId),
		  // "comments": null,
		  "isActive": true,
		  "insertedBy": parseInt(this.cookieService.get('userId')),
		  "insertedOn": new Date().toISOString,
		  "updatedBy": parseInt(this.cookieService.get('userId')),
		  "updatedOn":  new Date().toISOString,
		  "isInventoryManaged": true,
		  "sku": data.sku,
		  "preferredVendorId": Number(data.preferredVendorId),
		  "inventoryCategoryId":  Number(data.inventoryCategoryId),
		  "inventorySubCategoryId": Number(data.inventorySubCategoryId),
		  "materialType": 1,
		  "reorderTriggerlevel": Number(data.reorderTriggerlevel),
		  "reorderQuantity": Number(data.reorderQuantity),
		  "serialNo": data.serialNo,
		  "notes": data.notes,
		  // "customfields": null,
		  "warehouseId": Number(data.warehouseId)
		}
		  let params :any ={};
		   params.material = reqObj;
		  this.purchaseOrderService.updateMaterial(params).subscribe((res: any) => {
		  console.log(res, res);
		  if (res) {
			this.sharedService.setAlertMessage("Inventory Item updated successfully");
			this.isAssetLoaded = false;
		  //    this.row={};
		//   this.getMaterials();
		this.wareHouseIndex =-1;
		  }	
		});	
	}



	getAllMaterialType() {
		let queryParamBase: any = {};
		queryParamBase = {
			ApartmentId: this.cookieService.get('apartmentId'),
			LookupTypeId: 186
		};
		this.isAssetLoaded = true;
		this.lookupService.getLookupValueByLookupTypeId(queryParamBase).subscribe((res: any) => {
			if (res) {
				this.isAssetLoaded = false;
				this.materialTypeList = res ? res : [];
				
			}
		});
	}
