import { Component, OnInit,Injector } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';
import { PurchaseOrderService } from 'src/app/api/controllers/PurchaseOrder';
import { SharedService } from 'src/app/shared/services/shared.service';
import { InventoryService } from 'src/app/api/controllers/Inventory';
import { StaffService } from 'src/app/api/controllers/Staff';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ModalService } from '../../../../shared/services/modal.service';

@Component({
	selector: 'app-inventory-receiving',
	templateUrl: './inventory-receiving.component.html',
	styleUrls: ['./inventory-receiving.component.scss']
})
export class InventoryReceivingComponent implements OnInit {

	modalService: any;
	public asset: any = {};
	public receiving: any = {};
	public materialSetting: any = {};
	isAssetLoaded: boolean;
	warehouseData: any;
	public materialItemData: any = [];
	assetList: { id: number; itemName: string; }[];
	public assetSettings: any = {};
	public selectedItem = [];
	public staffList: any = [];
	public inventoryList:any =[];
	ItemStartIndex: any = 0;
	ItemEndIndex: any;
	totalItems: any;
	itemLimit: any = 5;
	isEdit :any
	issueToStaff:any =[];
	ngOnInit(): void {
		this.getAllGetAllWarehouse();
		this.getMaterials();
		this.getAllStaff();
		this.getAllReceivingItem();
		this.receiving.warehouseId = "";
		this.receiving.inGoodsincomingReceivedbyStaffId = "";
		this.materialSetting = {
			singleSelection: true,
			text: "Select inventory",
			selectAllText: 'Select All',
			unSelectAllText: 'UnSelect All',
			enableSearchFilter: true,
			badgeShowLimit: 3,
			// enableCheckAll:false,
			// showCheckbox:false,
			// lazyLoading: true
		};
		this.assetSettings = {
			singleSelection: true,
			text: "Search",
			selectAllText: 'Select All',
			unSelectAllText: 'UnSelect All',
			enableSearchFilter: true,
			badgeShowLimit: 3,
			// lazyLoading: true
		};
		this.sharedService.unitlistdeleteindexcast.subscribe(id => {
			if (id != null) {
			
					let param: any = {};
					param = {
						inventoryId: id,
						deleteBy: parseInt(this.cookieService.get('userId'))
					}
					this.inventoryService.deleteInventory(param).subscribe((res: any) => {
						setTimeout(() => {
							this.sharedService.setAlertMessage("Received item deleted successfully");
							this.sharedService.setUnitListDeleteIndex(null);
							// this.deleteType = '';
							// this.getAllInventory();
						}, 500)
					}, error => {
						console.log(error);
					});
				}
		});

	}


	constructor(
		private cookieService: CookieService,
		private purchaseOrderService: PurchaseOrderService,
		private sharedService: SharedService,
		private inventoryService: InventoryService,
		private staffService: StaffService,
		private lookupService:LookupService,
		private injector: Injector,		
	) {
		this.modalService = this.injector.get(ModalService);
	}

	onItemSelect(item: any) {
		this.receiving.materialId = item.id;
		this.getAvailableStock();
		this.getAllReceivingItem();
		console.log(item);
	}
	OnItemDeSelect(item: any) {
		console.log(item);
		console.log(this.selectedItems);
	}
	selectedItems(selectedItems: any) {
		console.log(selectedItems);
	}
	onSelectAll(items: any) {
		console.log(items);
	}
	onDeSelectAll(items: any) {
		console.log(items);
	}

	onItemSelectToStaff(item: any) {
		console.log(item);
		this.receiving.inGoodsincomingReceivedbyStaffId = item.id;
	}

	OnItemDeSelectToStaff(item: any) {
		console.log(item);
		console.log(this.selectedItems);
	}
	selectedItemsToStaff(selectedItems: any) {
		console.log(selectedItems);
	}
	onSelectAllToStaff(items: any) {
		console.log(items);
	}
	onDeSelectAllToStaff(items: any) {
		console.log(items);
	}

	deleteRow(index: number, data) {
		this.modalService.showConfirmModal(data.inventoryTransactionId);
	}
	setUpdate(list){
		const mainDiv = document.getElementById('outertop');
		// mainDiv.scrollTop = 0;
		mainDiv.scrollIntoView();
		this.receiving = list;
	
	}
	getAllGetAllWarehouse() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId')
		};
		this.isAssetLoaded = true;
		this.purchaseOrderService.getAllWarehouseByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				// this.warehouseData = [];
				this.warehouseData = res && res.length > 0 ? res : [];
			}
		});
	}

	getIndexParams(event) {
		this.ItemStartIndex = event.ItemStartIndex;
		this.ItemEndIndex = event.ItemEndIndex;
		this.itemLimit = event.itemLimit;
	}

	getAllStaff() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId')
		};
		this.isAssetLoaded = true;
		this.staffService.getAllStaffsByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				this.isAssetLoaded = false;
				// this.staffList = res ? res : [];
				if(res && res.length >0)
				{
				res.filter(val => {
					this.staffList.push({
						'id': val.staffId,
						'itemName': val.firstName
					});
				})
			  }
			}
		});
	}
	updateReceiving() {
		let receivingObj: any = {};
		receivingObj = {
			"inventoryTransactionId": this.receiving.inventoryTransactionId,
			"apartmentId": this.cookieService.get('apartmentId'),
			"inventoryTransactionTypeId": 12,
			"date": new Date().toISOString,
			"warehouseId": this.receiving.warehouseId,
			"materialId": this.receiving.materialId,
			"sku": this.receiving.sku,
			"transactionTypeId": 12,
			"availableStockQty": this.receiving.availableStockQty,
			"inPonumber": this.receiving.inPonumber,
			"inGoodReceiptNo": this.receiving.inGoodReceiptNo, // missing
			"inReceivingReason": this.receiving.inReceivingReason,
			"inReceivedStockQty": this.receiving.inReceivedStockQty,
			"inStockReceivedDate": this.receiving.inStockReceivedDate,
			"inCountQualityVerified": true,
			"inDamageReturnedQty": this.receiving.inDamageReturnedQty,
			"inActualStockMovedIn": this.receiving.inActualStockMovedIn,
			"inTotalCost": this.receiving.inTotalCost,
			"inGoodsincomingReceivedbyStaffId": this.receiving.inGoodsincomingReceivedbyStaffId,
			// "comments": "string",
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString,
			"updatedBy": parseInt(this.cookieService.get('userId')),
			"updatedOn": new Date().toISOString,
		}
		let params: any = {};
		params.inventoryTransaction = receivingObj;
		this.inventoryService.updateInventoryTransaction(params).subscribe((res: any) => {
			console.log(res, res);
			if (res) {
				this.sharedService.setAlertMessage("Receiving Item updated successfully");
				this.isAssetLoaded = false;
				this.receiving ={};
				this.getAllReceivingItem();
			}
		});
	}

	addReceiving() {
		let receivingObj: any = {};
		receivingObj = {
			"apartmentId": this.cookieService.get('apartmentId'),
			"inventoryTransactionTypeId": 12,
			"date": new Date().toISOString,
			"warehouseId": this.receiving.warehouseId,
			"materialId": this.receiving.materialId,
			"sku": this.receiving.sku,
			"transactionTypeId": 12,
			"availableStockQty": this.receiving.availableStockQty,
			"inPonumber": this.receiving.inPonumber,
			"inGoodReceiptNo": this.receiving.inGoodReceiptNo, // missing
			"inReceivingReason": this.receiving.inReceivingReason,
			"inReceivedStockQty": this.receiving.inReceivedStockQty,
			"inStockReceivedDate": this.receiving.inStockReceivedDate,
			"inCountQualityVerified": true,
			"inDamageReturnedQty": this.receiving.inDamageReturnedQty,
			"inActualStockMovedIn": this.receiving.inActualStockMovedIn,
			"inTotalCost": this.receiving.inTotalCost,
			"inGoodsincomingReceivedbyStaffId": this.receiving.inGoodsincomingReceivedbyStaffId,
			// "comments": "string",
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date(),
			"updatedBy": null,
			"updatedOn": new Date(),
		}
		let params: any = {};
		params.inventoryTransaction = receivingObj;
		this.inventoryService.addInventoryTransaction(params).subscribe((res: any) => {
			// console.log(res, res);
			if (res) {
				this.sharedService.setAlertMessage("Receiving Item added successfully");
				this.isAssetLoaded = false;
				this.receiving ={};
				this.selectedItem=[];
				this.issueToStaff=[];
				this.getAllReceivingItem();
				this.inventoryList =[];
				//   this.getMaterials();
			}
		});
	}

	getMaterials() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
		};
		this.purchaseOrderService.getMaterialsByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				if (res && res.length > 0) {
					res.filter(val => {
						this.materialItemData.push({
							'id': val.materialId,
							'itemName': val.material1
						});
					})
				}
			}
		});
	}
	getAvailableStock() {
		if (!this.receiving.materialId && !this.receiving.warehouseId) {
			return;
		}
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
			warehouseId :this.receiving.warehouseId,
            materialId:this.receiving.materialId
		};
		this.inventoryService.getStockCountbyMaterialId(queryParamBase).subscribe((res: any) => {
			
				
			if(  res && res.length > 0 ){
			 this.receiving.availableStockQty = res[0].totalStockQty;
			}
		});
	}
	getAllReceivingItem() {
		if(!this.receiving.warehouseId || !this.receiving.materialId ){
			return;
		}
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
			warehouseId:this.receiving.warehouseId,
			materialId:this.receiving.materialId
		};

		// this.isAssetLoaded = true;
		this.inventoryService.getAllInventoryTransactionByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				// this.warehouseData = [];
				this.inventoryList = res && res.length > 0 ? res : [];
				console.log(this.inventoryList);
				this.totalItems = this.inventoryList.length;
				if (this.totalItems > this.itemLimit) {
					this.ItemEndIndex = this.itemLimit;
				}
				else {
					this.ItemEndIndex = this.totalItems;
				}

			}
		});
	}



}
